<template><section><h1>脚本初始化</h1>
<hr>
<h2>示例</h2>
<p>项目入口文件
初始化脚本，将项目用到的服务插件都提前加载入脚本中</p>
<pre><code class="language-javascript"><span class="hljs-keyword">import</span> Vue <span class="hljs-keyword">from</span> <span class="hljs-string">'vue'</span>;
<span class="hljs-keyword">import</span> AMap <span class="hljs-keyword">from</span> <span class="hljs-string">'vue-amap'</span>;

<span class="hljs-keyword">import</span> App <span class="hljs-keyword">from</span> <span class="hljs-string">'./App.vue'</span>;
<span class="hljs-keyword">import</span> router <span class="hljs-keyword">from</span> <span class="hljs-string">'./router'</span>;

Vue.use(AMap);
AMap.initAMapApiLoader({
  <span class="hljs-attr">key</span>: <span class="hljs-string">'YOUR_KEY'</span>,
  <span class="hljs-attr">plugin</span>: [<span class="hljs-string">'AMap.Scale'</span>, <span class="hljs-string">'AMap.OverView'</span>, <span class="hljs-string">'AMap.ToolBar'</span>, <span class="hljs-string">'AMap.MapType'</span>,...]
});

<span class="hljs-keyword">const</span> app = <span class="hljs-keyword">new</span> Vue({  <span class="hljs-comment">// eslint-disable-line</span>
  router,
  <span class="hljs-attr">render</span>: <span class="hljs-function"><span class="hljs-params">h</span> =&gt;</span> h(App)
}).$mount(<span class="hljs-string">'#app'</span>);

</code></pre>
<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">lang</span>=<span class="hljs-string">"html"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">id</span>=<span class="hljs-string">"amap-cointainer"</span>&gt;</span>

  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">import</span> { lazyAMapApiLoaderInstance } <span class="hljs-keyword">from</span> <span class="hljs-string">'vue-amap'</span>;
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  mounted() {
    lazyAMapApiLoaderInstance.load().then(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
      <span class="hljs-keyword">this</span>.map = <span class="hljs-keyword">new</span> AMap.Map(<span class="hljs-string">'amap-cointainer'</span>, {<span class="hljs-attr">center</span>: <span class="hljs-keyword">new</span> AMap.LngLat(<span class="hljs-number">121.59996</span>, <span class="hljs-number">31.197646</span>)});
    });
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">style</span> <span class="hljs-attr">lang</span>=<span class="hljs-string">"css"</span>&gt;</span><span class="css">
<span class="hljs-selector-id">#amap-cointainer</span> {
  <span class="hljs-attribute">height</span>: <span class="hljs-number">200px</span>;
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>

</code></pre>
<h2>提示</h2>
<p>该 <code>promise</code> 只是地图脚本的初始化，作为定制化较高（如服务）的需求；组件直接使用无需；</p>
<p><demo></demo></p>

</section></template>

<script>
import Demo from 'demos/init.vue';
export default {
  components: {
    Demo
  }
}
</script>


